.dashboard-wrapper {
    padding-right: 50px;
    display: grid;
    grid-template-columns: minmax(260px, 1fr) 5.5fr;
    grid-gap: 24px;
    background: linear-gradient(198.65deg, rgba(193, 227, 211, 0.52) 12.62%, rgba(245, 248, 252, 0.45) 41.35%), #FBF3E5;
    min-height: 100vh;
    .sidebar {
        background: #FFFFFF;
        border-right: 1px solid #F3F3F3;
        padding: 40px 16px 54px;
        display: flex;
        flex-direction: column;
        .logo {
            margin-left: 24px;
            margin-bottom: 49px;
        }
        &__inner {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: #7F879E;
            .title {
                height: 48px;
                padding: 0 20px;
                display: flex;
                align-items: center;
                justify-content: start;
                border-radius: 10px;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                background: #47D18C;
                color: #FFFFFF;
                margin-bottom: 16px;
                svg {
                    margin-right: 14px;
                }
            }
            &>ul {
                li {
                    &:not(:last-child) {
                        margin-bottom: 15px;
                    }
                    &.mb-item {
                        margin-bottom: 50px;
                    }
                    height: 48px;
                    .sidebar-item {
                        cursor: pointer;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        padding: 0 20px;
                        svg {
                            margin-right: 12px;
                        }
                        img {
                            margin-left: auto;
                        }
                        .active-value {
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 130%;
                            color: #47D18C;
                            padding: 0 10px;
                            margin-left: auto;
                        }
                    }
                }
            }
        }
        .logout-btn {
            height: 48px;
            display: flex;
            align-items: center;
            padding: 0 20px;
            margin-top: auto;
            color: #F05817;
            svg {
                margin-right: 12px;
            }
        }
    }
    .header {
        margin: 40px 0;
        display: flex;
        align-items: center;
        .search-wrapper {
            height: 48px;
            background: #FFFFFF;
            border-radius: 12px;
            position: relative;
            padding: 4px;
            max-width: 384px;
            flex: 0 0 33%;
            display: flex;
            align-items: center;
            &>svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 16px;
            }
            input {
                flex: 1;
                padding-left: 53px;
            }
        }
        .select-wrapper {
            width: 118px;
            height: 100%;
            background: #EAEDEE;
            border-radius: 10px;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #1B2124;
            position: relative;
            select {
                border: none;
                padding: 0 14px;
                background: transparent;
                height: 100%;
                width: 100%;
                @include selectDefault;
            }
            svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 14px;

            }
        }
        .notification,
        .settings,
        .user {
            width: 49px;
            height: 48px;
            flex: 0 0 48px;
            background: #FFFFFF;
            box-shadow: 0px 18px 54px rgba(0, 0, 0, 0.02);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            cursor: pointer;
        }
        .settings {
            margin-right: 28px;
        }
        .notification {
            margin-right: 28px;
            margin-left: auto;
            &::after {
                content: attr(data-count);
                position: absolute;
                width: 28px;
                height: 19px;
                right: -10px;
                top: 0;
                background: #FE6E66;
                border-radius: 100px;
                font-weight: 500;
                font-size: 12px;
                text-align: center;
                line-height: 18px;            
                color: #FFFFFF;
            }
        }
        .burger {
            display: none;
            margin-right: 15px;
            @include burger;
        }
        .header-logo {
            display: none;
        }
    }

    .content {
        &__inner {
            display: grid;
            grid-gap: 24px;
            grid-template-columns: 3fr 1.25fr;
            &-base>* {
                &:not(:last-child) {
                    margin-bottom: 40px;
                }
            }
        }
        &__top {
            display: grid;
            grid-template-columns: repeat(3,1fr);
            grid-gap: 24px;
            color: #7F879E;
            &-item {
                padding: 24px 22px;
                background: #FFFFFF;
                border-radius: 12px;
            }
            .row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 20px;
            }
            h3 {
                color: $setBlack1;
            }
        }
        &__chart {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 33px 24px;
        }
        .title-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .chart-info {
                display: flex;
                align-items: center;
                ul {
                    display: flex;
                    align-items: center;
                    margin-right: 45px;
                    li {
                        display: flex;
                        align-items: center;
                        &:first-child {
                            margin-right: 16px;
                            span {
                                background: #1F8FFF;
                            }
                        }
                        &:last-child {
                            span {
                                background: #78C58C;
                            }
                        }
                        span {
                            display: block;
                            width: 8px;
                            height: 8px;
                            border-radius: 50%;
                            margin-right: 8px;
                        }
                    }
                }
            }
        }
    }
}