@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300;400;500;600;700&display=swap');

@import 'variables';
@import 'mixins/mixin';
@import 'mixins/burger';
@import 'pages/auth';
@import 'pages/dashboard';
@import 'components/quiz';
@import 'components/main';
@import 'components/menu';
@import 'components/popup';
@import 'components/header';
@import 'components/footer';

*,
*::before,
*::after {
  box-sizing: border-box;  
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -khtml-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;  

}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html, body {
  height: 100%;
}

body {
  position: relative;
  min-width: 320px;
  font-family: 'Readex Pro', sans-serif;
  text-rendering: optimizeSpeed;
  font-size: 14px;
  line-height: 130%;
  color: $black1;
  overflow-x: hidden;
  font-weight: 400;
  &.fixed {
    overflow: hidden;
  }
}
a {
  text-decoration: none;
  color: inherit;
}
ul[class],
ol[class] {
  list-style: none;
}

a {
  text-decoration-skip-ink: auto;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

button {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: 0;
  outline: none;
  cursor: pointer;
}

input {
  outline: none;
  border: 0;
  padding: 0;
}

input,
button,
textarea,
select {
  font: inherit;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

// heading 1

h1 {
  font-weight: 600;
  font-size: 62px;
  line-height: 130%;
}
h2 {
  font-weight: 600;
  font-size: 44px;
  line-height: 130%;
}
h3 {
  font-weight: 700;
  font-size: 32px;
  line-height: 130%;
  @include max1200 {
    font-size: 28px;
  }
  @include max576 {
    font-size: 24px;
  }
}
h4 {
  font-weight: 700;
  font-size: 28px;
  line-height: 130%;
}
h5 {
  font-weight: 700;
  font-size: 24px;
  line-height: 130%;
}
h6 {
  font-weight: 700;
  font-size: 20px;
  line-height: 130%;
  @include max576 {
    font-size: 18px;
  }
}

// heading 2

.h1 {
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
}
.h2 {
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
}
.h3 {
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
}
.h4 {
  font-weight: 700;
  font-size: 12px;
  line-height: 130%;
}

// labels

.label1 {
  font-weight: 600;
  font-size: 22px;
  line-height: 130%
}

.label2 {
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
}

.label3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  @include max576 {
    font-size: 14px;
  }
}

.label4 {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  @include max576 {
    font-size: 14px;
    line-height: 130%;
  }
}

.label5 {
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
}

.label6 {
  font-weight: 600;
  font-size: 12px;
  line-height: 130%;
}


// paragraphs

.p1 {
  font-size: 18px;
  line-height: 160%;
  @include max1200 {
    font-size: 16px;
  }
  @include max576 {
    font-size: 14px;
  }
}
.p2 {
  font-size: 16px;
  line-height: 160%;
  @include max576 {
      font-size: 14px;
      line-height: 18px;
  }
}

.p3 {
  font-size: 14px;
  line-height: 160%;
}

.p4 {
  font-size: 12px;
  line-height: 160%;
}

.pm {
  font-weight: 500;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}



.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all .3s ease;
  border-radius: 12px;
  border: 3px solid transparent;
  color: white;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
}
.primary {
  background: $setBlack3;
  &:hover,
  &:active {
    background: $setBlack1;
  }
  &:focus {
    background: $setBlack1;
    border: 3px solid $grey2;
  }
}
.secondary {
  background: $green2;
  &:hover,
  &:active {
    background: $green1;
  }
  &:focus {
    background: $green1;
    border: 3px solid #269D62;
  }
}
.neutral {
  background: $lightgrey2;
  color: $grey1;
  &:hover,
  &:active {
    background: $lightgrey1;
  }
  &:focus {
    background: $lightgrey1;
    border: 3px solid $grey3;
  }
}
.bordered {
  background: #FFFFFF;
  color: $grey1;
  border: 1px solid #AAAFBE;
  &:hover,
  &:active {
    border: 1px solid #7F879E;
    color: $grey3;
  }
  &:focus {
    border: 3px solid #D4D7DF;
    color: $grey3;
  }
}
.rounded {
  border-radius: 50%;
}
.text-center {
  text-align: center;
}