@function rem($px) {
	@return ($px / 16)+rem;
}
@mixin selectDefault {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
}

@mixin placeholder {
	&::-webkit-input-placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}
}

