.onboarding-popup {
    max-width: 1320px;
    width: calc(100% - 40px);
    background: #F8F5EF;
    box-shadow: 0px 4px 100px rgba(127, 135, 158, 0.15);
    border-radius: 30px;
    overflow: hidden;
    color: $grey1;
    // display: block !important;
    position: relative;
    display: none;

    &.open {
        display: block;
    }

    @include max576 {
        border-radius: 12px;
    }

    &__inner {
        position: relative;

        .close-a {
            cursor: pointer;
            font-weight: 600;
            font-size: 12px;
            line-height: 130%;
            position: absolute;
            top: 60px;
            right: 75px;

            @include max768 {
                top: 30px;
                right: 30px;
            }

            @include max576 {
                top: 24px;
            }
        }
    }

    .onboarding-start {
        padding: 250px 50px;

        @include max992 {
            padding: 150px 50px;
        }

        @include max576 {
            padding: 35px 20px;
        }

        &__bg img {
            position: absolute;
            top: 0;
            left: 0;
            width: 55%;
            z-index: -1;

            @include max576 {
                width: 100%;
            }
        }

    }

    .onboarding-right {
        margin-left: 43%;

        @include max992 {
            margin-left: 20%;
        }

        @include max768 {
            margin-left: 10%;
        }

        @include max576 {
            margin-left: 20px;
            height: 100%;
            height: 100%;
            min-height: calc(100vh - 140px);
            max-height: 760px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
        }

        &__title {
            font-weight: 600;
            font-size: 56px;
            line-height: 60px;
            letter-spacing: -0.6px;
            color: #231F20;
            max-width: 400px;

            @include max768 {
                font-size: 36px;
                line-height: 40px;
            }

            @include max576 {
                font-size: 24px;
                line-height: 30px;
                max-width: 180px;
            }
        }

        p {
            font-weight: 400;
            font-size: 20px;
            line-height: 60px;
            letter-spacing: -0.6px;
            color: #7F879E;
            margin: 38px 0 56px;

            @include max992 {
                font-size: 18px;
                line-height: 20px;
            }

            @include max768 {
                font-size: 16px;
                line-height: 20px;
                margin: 20px 0 36px;
            }

            @include max576 {
                font-size: 14px;
                line-height: 160%;
                font-weight: 500;
                max-width: 240px;
                margin-bottom: 60px;
            }
        }

        .btn {
            background: $setBlack2;
            height: 44px;
            padding: 0 10px;
            width: 100%;
            max-width: 128px;
            font-size: 12px;
            line-height: 130%;

            @include max576 {
                position: absolute;
                bottom: 0;
                left: 0;
                max-width: 100%;
            }
        }
    }

    &.onboarding-edit__lang {
        z-index: 104;
        height: fit-content;
        margin: auto;
        background: #F8F5EF;
        box-shadow: 0px 4px 100px rgba(127, 135, 158, 0.15);
        border-radius: 12px;
        position: relative;
        padding: 25px 20px;
        color: $black1;
        min-height: calc(100vh - 70px);

        .top-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 40px;
        }

        label {
            display: block;
            margin-bottom: 12px;
        }

        .remove__lang {
            margin-top: 50px;
            margin-bottom: 100px;
            color: $red2;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            cursor: pointer;

            svg {
                margin-right: 6px;
            }
        }

        .close_lang {
            margin-right: 15px;
            cursor: pointer;
        }

        .select-wrapper {
            position: relative;
            height: 100%;
            flex: 0 0 50%;
            margin-bottom: 22px;

            &::before {
                content: url(../img/icons/arrow-down.svg);
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 12px;
                height: 15px;
            }
        }

        select {
            width: 100%;
            padding: 0 12px;
            background: white;
            border: none;
            height: 44px;
            border-radius: 12px;
            @include selectDefault;
            option {
                &[disabled] {
                    color: #AAAFBE;
                }
            }

            &::-ms-expand {
                display: none;
            }

            &:first-child {
                border-right: 1.5px solid #F3F3F3;
            }
        }
    }
}

.onboarding-quiz {
    display: grid;
    grid-template-columns: 1fr 2fr;

    @include max768 {
        grid-template-columns: 1fr;
    }

    &__left {
        background-image: url(../img/bg/q1.svg);
        background-size: cover;
        background-repeat: no-repeat;
        display: flex;
        flex-direction: column;
        padding: 100px 20px 100px 90px;

        @include max1200 {
            padding: 60px 20px 60px 60px;
        }

        @include max992 {
            padding: 40px 20px 40px 40px;
        }

        @include max576 {
            padding: 20px 22px;
        }

        .number {
            font-weight: 700;
            font-size: 24px;
            line-height: 150%;
            color: #2D2D2D;
            width: 58px;
            height: 58px;
            border: 2px solid #2D2D2D;
            display: flex;
            align-items: center;
            justify-content: center;

            @include max1200 {
                font-size: 20px;
                width: 48px;
                height: 48px;
            }

            @include max576 {
                font-size: 12px;
                line-height: 130%;
                width: 27px;
                height: 27px;
            }
        }
    }

    &__right {
        color: #231F20;
        padding: 92px 100px 66px 90px;
        display: flex;
        flex-direction: column;
        background: #F8F5EF;
        box-shadow: 0px 4px 100px rgba(127, 135, 158, 0.15);

        // border-radius: 30px;
        @include max1200 {
            padding: 62px 60px 46px 60px;
        }

        @include max992 {
            padding: 46px;
        }

        @include max576 {
            padding: 24px 20px 35px;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: -0.43px;
        margin-bottom: 23px;
        margin-top: 68px;
        width: fit-content;
        color: #231F20;

        @include max1200 {
            font-size: 30px;
            line-height: 38px;
        }

        @include max992 {
            font-size: 25px;
            line-height: 28px;
            margin-top: 40px;
            margin-bottom: 15px;
        }

        @include max576 {
            font-size: 20px;
            line-height: 25px;
            margin: 20px 0 8px;
        }
    }

    p {
        margin-bottom: 50px;

        @include max992 {
            font-size: 14px;
        }

        @include max576 {
            font-size: 12px;
        }
    }

    &__nav {
        margin-top: 340px;
        display: flex;
        align-items: center;

        @include max992 {
            margin-top: auto;
        }

        span {
            display: flex;
            flex: 0 0 8px;
            height: 8px;
            border-radius: 50%;
            background: white;

            &.active {
                background: #231F20;
            }

            &:not(:last-child) {
                margin-right: 26px;

                @include max768 {
                    margin-right: 16px;
                }
            }
        }
    }

    .input-group {
        margin-bottom: 26px;

        @include max992 {
            margin-bottom: 20px;
        }

        &.row-group {
            display: grid;
            grid-gap: 15px;
            grid-template-columns: 2fr 1fr;

            @include max992 {
                grid-template-columns: 1fr;
                grid-gap: 20px;
            }
        }

        label {
            margin-bottom: 16px;
            display: block;

            @include max576 {
                margin-bottom: 12px;
            }
        }

        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 21px;

            @include max768 {
                margin-bottom: 12px;
            }

            label {
                margin-bottom: 0;
            }
        }

    }

    .checkbox {
        width: 20px;
        height: 20px;
        border: 1.5px solid #7F879E;
        border-radius: 6px;
        position: relative;
        margin-right: 10px;
        transition: all .3s ease;

        &:hover {
            border: 1.5px solid #47D18C;
        }
    }

    .custom-checkbox {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        z-index: 11;
        cursor: pointer;
    }

    .tick {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
        opacity: 0;
        width: 22px;
        height: 22px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .custom-checkbox:checked+.tick {
        opacity: 1;
    }

    .checkbox-item {
        display: flex;
        align-items: center;

        label {
            margin: 0;
        }
    }

    .input-counter {
        background: #FFFFFF;
        border: 1px solid #D4D7DF;
        border-radius: 12px;
        display: grid;
        grid-template-columns: 1fr 40% 1fr;
        height: 60px;
        max-width: 295px;

        @include max768 {
            height: 50px;
            grid-template-columns: 1fr 60% 1fr;
        }

        @include max576 {
            height: 44px;
            font-size: 16px;
            line-height: 24px;
        }

        &>* {
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
        }
    }

    .quiz-nav {
        margin-top: auto;
        display: flex;

        @include max576 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 7px;
        }

        button {
            height: 44px;
            min-width: 128px;
            font-size: 12px;
            line-height: 20px;
            margin-top: 20px;

            @include max576 {
                height: 44px;
            }

            svg {
                margin-left: 4px;
                margin-right: 0;
            }
        }

        button:first-child {
            margin-right: 12px;

            @include max576 {
                border: 2px solid #D9D9D9;
                margin-right: 0;
            }

            svg {
                margin-right: 8px;
                margin-left: 0;
            }
        }
    }

    .input-field {
        background: #FFFFFF;
        border: 1px solid #D4D7DF;
        border-radius: 12px;
        width: 100%;
        overflow: hidden;
        padding: 0 7px;
        height: fit-content;
        display: flex;
        align-items: center;

        input {
            margin: 0 8px;
            width: 180px;
            padding: 10px 0;

            @include placeholder {
                color: $grey2;
            }

            @include max768 {
                font-size: 12px;
                line-height: 130%;
                padding: 5px 0;
            }
        }
    }

    .added-values {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        @include max768 {
            padding: 5px 0;
        }

        .value {
            padding: 0 14px;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            border-radius: 12px;
            margin: 10px 3px;
            height: 38px;
            background: #DFEFFF;

            @include max1200 {
                height: 32px;
            }

            @include max992 {
                font-size: 12px;
            }

            @include max768 {
                margin: 5px;
                border-radius: 8px;
            }

            &:nth-child(even) {
                background: #FEE5A4;
            }

            svg {
                margin-left: 8px;
                cursor: pointer;
            }
        }
    }

    .add_btn {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: $blue1;
        font-weight: 500;
        display: flex;
        align-items: center;

        @include max576 {
            font-size: 12px;
        }

        svg {
            margin-left: 4px;
        }
    }

    .selector-group {
        position: relative;

        &.desktop-selector {
            @include max768 {
                display: none;
            }
        }

        &.mobile-selector {
            display: none;

            @include max768 {
                display: block;
            }
        }

        &__row {
            background: #FFFFFF;
            border: 1px solid #D4D7DF;
            border-radius: 12px;
            margin-bottom: 10px;
            position: relative;

            &.disabled {
                color: $grey2;

                * {
                    @include placeholder {
                        color: $grey2;
                    }
                }
            }

            &-inner {
                display: flex;
                border-radius: 12px;
                overflow: hidden;

                .select-wrapper {
                    position: relative;
                    height: 100%;
                    flex: 0 0 50%;

                    &::before {
                        content: url(../img/icons/arrow-down.svg);
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 30px;
                        height: 15px;
                    }

                    &:first-child:after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 0;
                        height: calc(100% - 20px);
                        width: 1.5px;
                        background: $lightgrey1;
                    }
                }

                select {
                    width: 100%;
                    padding: 0 29px;
                    background: white;
                    border: none;
                    height: 60px;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    appearance: none;
                    outline: none;

                    @include max1200 {
                        height: 50px;
                    }

                    option {
                        &[disabled] {
                            color: #AAAFBE;
                        }
                    }

                    &::-ms-expand {
                        display: none;
                    }

                    &:first-child {
                        border-right: 1.5px solid #F3F3F3;
                    }
                }
            }

            &.lang_select-js {
                padding: 0 20px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 64px;
                cursor: pointer;

                .info {
                    p {
                        margin: 0;

                        &:first-child {
                            font-size: 16px;
                        }

                        &:last-child {
                            margin-top: 4px;
                            font-size: 12px;
                            color: #8D9092;
                        }
                    }
                }
            }
        }

        .trash {
            cursor: pointer;
            position: absolute;
            top: 50%;
            right: -40px;
            transform: translateY(-50%);

            @include max992 {
                right: -28px;
            }
        }

    }

    .mt-5 {
        margin-top: 50px;

        @include max768 {
            margin-top: 0;
        }
    }

    .mobile-divided {
        border: none;
        background: none;

        @include max576 {
            .selector-group__row-inner {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-gap: 6px;

                input,
                select {
                    padding: 0 12px;
                    font-size: 14px;
                    line-height: 130%;
                    height: 40px;
                }

                .select-wrapper::before {
                    right: 12px;
                }
            }

            input,
            .select-wrapper {
                background: white;
                border-radius: 12px;
                overflow: hidden;
                border: 1px solid #D4D7DF;
            }

        }
    }

    .select-items {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: -12px;

        @include max576 {
            margin-top: -8px;
        }
    }

    .select-item {
        margin-top: 12px;
        margin-right: 12px;
        height: 50px;
        font-weight: 700;
        font-size: 16px;
        line-height: 30px;
        padding: 0 22px;

        @include max576 {
            height: 32px;
            font-size: 12px;
            line-height: 15px;
            padding: 0 8px;
            margin-top: 8px;
            margin-right: 8px;
            border-radius: 8px;
        }

        &.selected,
        &:hover {
            background: #47D18C;
            border: 1px solid #D4D7DF;
            box-shadow: 0px 4px 20px rgba(127, 135, 158, 0.13);
            color: white;
        }
    }

    .input-merged {
        flex: 0 0 50%;
        position: relative;

        input {
            height: 100%;
            width: 100%;
            padding: 0 30px;
            font-size: 16px;

            @include placeholder {
                color: #868686;
                font-size: 16px;

                @include max576 {
                    font-size: 14px;
                    line-height: 160%;
                }
            }
        }

        &::after {
            content: "";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            height: calc(100% - 20px);
            width: 1.5px;
            background: $lightgrey1;
        }
    }

    .input-wrapper {
        position: relative;

        .error-text {
            position: absolute;
            top: calc(100% + 4px);
            left: 0;
            color: $red1;
            opacity: 0;
            pointer-events: none;
        }

        &.error {
            input {
                border-color: $red3;
                background: white;
                color: $red1;
            }

            input+svg {
                path {
                    stroke: $red1;
                }
            }
        }

        .icon-input {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 22px;

            @include max576 {
                left: 15px;
            }

            &+input {
                padding-left: 53px;

                @include max576 {
                    padding-left: 47px;
                }
            }
        }

        input {
            background: #FFFFFF;
            border: 1px solid #D4D7DF;
            border-radius: 12px;
            height: 60px;
            padding: 0 24px;
            width: 100%;
            font-size: 16px;

            @include placeholder {
                color: #868686;
                font-size: 16px;

                @include max576 {
                    font-size: 14px;
                    line-height: 160%;
                }
            }

            &:not(:placeholder-shown) {
                border: 2px solid $grey3;
            }

            &:hover {
                background: $lightgrey1;
            }

            &:focus {
                background: $lightgrey3;
                border: 2px solid $green3;
            }

            &:active {
                background: white;
                border: 2px solid $grey3;
            }

            @include max1200 {
                height: 50px;
            }

            @include max576 {
                height: 44px;
                font-size: 14px;
                line-height: 160%;
                padding-left: 15px;
            }
        }
    }

    .input-range-container {
        margin-bottom: 88px;
        @include max768 {
            margin-bottom: 50px;
        }
    }
    .range-bar {
        width: 100%;
        height: 8px;
        background: #D9D9D9;
        border-radius: 10px;
        position: relative;
    }

    .thumb {
        height: 20px;
        width: 20px;
        background: #FFFFFF;
        border: 6px solid #47D18C;
        box-shadow: 0px 4px 100px rgba(127, 135, 158, 0.15);
        border-radius: 50%;
        cursor: pointer;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
    }

    .min-thumb {
        left: 0;
    }

    .max-thumb {
        right: 0;
    }
    .min-value,
    .max-value {
        position: absolute;
        top: calc(100% + 10px);
        left: 50%;
        transform: translateX(-50%);
        font-weight: 400;
        font-size: 12px;
        line-height: 30px;
        color: #8D9092;
        display: flex;
        span {
            margin-left: 4px;
        }
    }
    .range-selection {
        height: 100%;
        background: #47D18C;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 0;
    }


    .input-row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        grid-gap: 25px;
        @include max576 {
            grid-template-columns: 1fr;
            grid-gap: 15px;
        }
        .input-wrapper {
            border-radius: 12px;
            position: relative;

            svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 20px;
            }

            input {
                padding-left: 57px;
            }
        }
    }



    .select-wrapper {
        position: relative;

        &.has-icon {
            select {
                padding-left: 57px;
                @include max576 {
                    padding-left: 50px;
                }
            }
        }

        .select-icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 20px;
        }
    }
}