.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all .3s ease;
    opacity: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
    padding: 20px 0;
    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.layout {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 101;
    background: rgba(37, 42, 63, 0.4);

}
.popup-object {
    background: white;
    z-index: 102;
    display: none;
    height: fit-content;
    margin: auto;
    &.open {
        display: block;
    }
}
.auth-success {
    max-width: 872px;
    border-radius: 20px;
    width: calc(100% - 40px);
    padding: 88px 20px;
    @include max576 {
        padding: 40px 20px 52px;
    }
    &__inner {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        max-width: 460px;
        text-align: center;
        margin: 0 auto;
        .rounded {
            background: rgba(52, 168, 83, 0.1);
            width: 100px;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p {
            color: $grey1;
        }
        h6 {
            margin: 27px 0 16px;
            @include max576 {
                margin-top: 39px;
            }
        }
        .auth-btn {
            margin: 32px 0 0;
            max-width: 340px;
            width: 100%;
            height: 44px;
            @include max576 {
                margin-top: 36px;
            }
        }
    }
}
